.editor-wrapper {
    width: 96;
    margin-top: 30px;
    margin-bottom: 30px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.editor-heading {
    border-bottom: 1px solid #666;
    border-top: 1px solid #666;
    width: 100%;
    text-align: center;
    height: 26px;
    width: 504px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    box-shadow: 3px 4px 5px 3px rgba(0, 0, 0, 0.3)
}

.lisa {
    height: 26px;
    width: auto;
}

.editor {
    width: 500px;
    height: 200px;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    background-color: #eaeaa4;
}
